<template>
    <div class="category-index">
        <div class="com-title-bar">
            <div class="box van-hairline--bottom">
                <div class="back" @click="$routerBack"></div>
                <div class="title">全部分类</div>
                <div class="action"></div>
            </div>
        </div>
        <div class="area">
            <div class="item" v-for="(item,index) in area.data" :key="index" @click="categoryHandel(item.id,item.title)">
                <div class="background">
                    <img :src="item.image" alt=""/>
                </div>
                <div class="box">
                    <div class="title">
                        {{ item.title }}
                    </div>
                    <div class="abstract">
                        {{ item.abstract }}
                    </div>
                    <div class="tags">
                        <div class="tag" v-for="(tag,key) in item.tags" :key="key">
                            <div>{{ tag }}</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                area: {
                    data: []
                }
            }
        },
        mounted() {
            this.initArea();
        },
        methods: {
            initArea() {
                this.$axios.get('/api/app/category/area')
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.area.data = result;
                        }
                    });
            },
            categoryHandel(area, title) {
                this.$router.push('/category/data?area=' + area + '&title=' + title);
            }
        }
    }
</script>

